











import {Component, Prop, Vue} from "vue-property-decorator"

@Component({ components: { } })
export default class HyModal extends Vue {
	@Prop()
	private modalText!: string

	@Prop({ type: Boolean })
	private bgTransparent!: boolean

	@Prop({ type: Boolean })
	private mx!: boolean

	@Prop({ type: Boolean })
	private selfClose!: boolean

	@Prop()
	private modalType!: string

	private closeModal(): void {
		if (this.selfClose)
			this.$emit("close")
	}
}
